"use client";

import { useEffect, useState } from "react";

const STATUS_URL =
  "https://hallucination.calmwave-93bbec10.brazilsouth.azurecontainerapps.io/status";

export default function PeopleCounter() {
  const [count, setCount] = useState<number>(0);

  useEffect(() => {
    const fetchStatus = async () => {
      try {
        const res = await fetch(STATUS_URL, { cache: "no-store" });
        const data = await res.json();
        setCount(data.people_detected ?? 0);
      } catch {
        setCount(0);
      }
    };

    fetchStatus();
    const interval = setInterval(fetchStatus, 1000);
    return () => clearInterval(interval);
  }, []);

  return (
    <div className="bg-zinc-900 border border-zinc-700 rounded-xl p-6">
      <h2 className="text-sm text-zinc-400 uppercase tracking-wide">
        People Detected
      </h2>
      <p className="mt-2 text-5xl font-bold text-green-400">{count}</p>
    </div>
  );
}
