"use client";

import { useEffect, useState } from "react";

const STATUS_URL =
  "https://hallucination.calmwave-93bbec10.brazilsouth.azurecontainerapps.io/status";

export default function SceneState() {
  const [scene, setScene] = useState<string>("UNKNOWN");

  useEffect(() => {
    const fetchStatus = async () => {
      try {
        const res = await fetch(STATUS_URL, { cache: "no-store" });
        const data = await res.json();
        setScene(data.scene_state ?? "UNKNOWN");
      } catch {
        setScene("UNKNOWN");
      }
    };

    fetchStatus();
    const interval = setInterval(fetchStatus, 1000);
    return () => clearInterval(interval);
  }, []);

  return (
    <div className="bg-zinc-900 border border-zinc-700 rounded-xl p-6">
      <h2 className="text-sm text-zinc-400 uppercase tracking-wide">
        Scene State
      </h2>
      <p className="mt-3 text-xl font-semibold text-blue-400">
        {scene.replaceAll("_", " ")}
      </p>
    </div>
  );
}
